.wrapper {
  margin-top: 0px;
  display: grid;
  grid-template-columns: 1fr;
  grid-column-gap: 1px;
  background-color: #ececec;
   margin-bottom: 10px;
}

.letter {
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px;
  font-size: 10px;
  color: white;
  line-height: 1;
  font-family: 'hobeaux-rococeaux-background', Helvetica;
  transition: all .3s ease;
  box-shadow: 5px black;
  margin-right: 0px;
  margin-left: 0px;
  position: relative;
}

.linkprop:hover{
  text-decoration: none;
  color: white;
}

/*.fadde {
